.form {
  display: flex;
  justify-content: center;
  /* height: 100vh; */
  /* max-width: 900px; */
  /* margin: 15px; */
  align-items: center;
  justify-content: center;
  position: relative;
}

.form a {
  margin: 15px;
  color: white;
}

.background {
  position: fixed;
  width: 120%;
  height: 120%;
  top: -65%;
  left: -2%;
  z-index: -1;
  border-radius: 45px;
  transform: rotate(9deg);
  background: #503f9d 0 0 repeat;
}

.persistcheck {
  align-items: center;
  display: flex;
  height: 25px;
}
.persistcheck label {
  margin-left: 5px;
}

