.instructions {
  font-size: 0.75rem;
  border-radius: 0.5rem;
  background: #000;
  color: #fff;
  padding: 0.25rem;
  position: relative;
  bottom: -10px;
}

.instructions > svg {
  margin-right: 0.25rem;
}

.offscreen {
  position: absolute;
  left: -9999px;
}

.hide {
  display: none;
}

.valid {
  color: limegreen;
  margin-left: 0.25rem;
}

.invalid {
  color: red;
  margin-left: 0.25rem;
}

.errmsg {
  background-color: lightpink;
  color: firebrick;
  font-weight: bold;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
}

.line {
  display: inline-block;
}

.welcome {
  display: flex;
  justify-content: center;
  /* height: 100vh; */
  /* max-width: 900px; */
  /* margin: 15px; */
  align-items: center;
  justify-content: center;
  position: relative;
}

.loginRegisterContainer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 950px;
  min-width: 400px;
  flex-wrap: wrap;
}

.item {
  display: flex;
  flex-direction: column;
  padding: 5px;
  justify-content: flex-start;
  align-items: flex-start;
  max-width: 450px;
  margin: 15px;
  padding: 10px;
  min-width: 350px;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.headerline {
  display: flex;
  flex-direction: column;
  max-width: 250px;
  margin: 25px;
  min-width: 250px;
  align-items: baseline;
  white-space: pre-wrap;
}

.headerS {
  line-height: 28px;
  font-size: 24px;

}
.headerText {
  line-height: 23px;
  font-size: 17px;
  white-space: pre-wrap;
}

.text {
  line-height: 23px;
  font-size: 15px;
  white-space: pre-wrap;
  cursor: pointer;
  margin-bottom: 5px;
}

.politika {
  display: flex;
  white-space: pre-wrap;
}
