.clbtn2 {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  height: 10px;
}
.clbtn2 div {
  cursor: pointer;
  position: relative;
  height: 68px;
  width: 40px;
}
.clbtn2 .leftright {
  height: 4px;
  width: 30px;
  position: absolute;
  margin-top: 24px;
  background-color: #46484b;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 0.3s ease-in;
}
.clbtn2 .rightleft {
  height: 4px;
  width: 30px;
  position: absolute;
  margin-top: 24px;
  background-color: #46484b;
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all 0.3s ease-in;
}
.clbtn2 .closebtn {
  margin: 54px 0 0 -5px;
  position: absolute;
  color: #46484b;
  font-family: Tahoma, sans-serif;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s ease-in;
  opacity: 0;
}
.clbtn2 div:hover .leftright {
  transform: rotate(-45deg);
  background-color: #000;
}
.clbtn2 div:hover .rightleft {
  transform: rotate(45deg);
  background-color: #000;
}
.clbtn2 div:hover .closebtn {
  opacity: 1;
}
