.≈ {
    display: flex;
    flex-direction: column;
    width: 600px;
    margin: 15px;
    padding-left: 10px;
    border-radius: 10px;
  }
  
  .formaData {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 0px 0 10px 0px;
    padding: 30px 15px 30px 15px;
  }
  
  .row {
    display: flex;
    margin: 20px 0px 0px 0px;
  }
  
  .formaDataRow {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background: #fff;
    border-radius: 25px;
    align-items: center;
  }
  .rowSave {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    background: #fff;
    border-radius: 25px;
  }
  
  .changing {
    font-family: Verdana, sans-serif;
    height: 35px;
    width: 200px;
    justify-content: center;
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    align-items: baseline;
    cursor: pointer;
    color: rgb(17, 16, 16);
    border: 2px;
    box-shadow: 0px 1px 5px #a09c8e;
    border-radius: 5px;
    background-color: #78c0ff;
  }
  
  .changing:hover {
    box-shadow: 0 0 10px 2px #131312;
    border-radius: 5px;
  }
  
  .formaDataList {
    display: flex;
    margin: 10px 15px 15px;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }
  
  .modal {
    position: absolute;
    width: 40vw;
    z-index: 120;
    overflow: hidden;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3px;
    background: #fff;
    border-radius: 25px;
    box-shadow: 0 7px 20px rgb(100 28 2 / 14%);
    min-width: 826px;
    max-width: 1200px;
  }
  
  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
  }
  
  button {
    margin: 12px;
  }
  
  .listfiles {
    display: flex;
    flex-direction: column;
  }
  